.phone-numer {
  white-space: nowrap;
  display: none;
  position: absolute;
  margin-block: 0;
  transform: translateX(-100%);
  margin-left: -8px;
  align-items: center;
  height: 100%;
  width: auto;
  color: $main-text-color;

  font-family: Inter;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 2px;
}

.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../images/icons/phone-number.svg);
    position: relative;

    @include on-desktop {
      &:hover .phone-numer {
       display: flex;
      }
    }

  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.svg);
  }
}


