.contact {
  display: flex;
  flex-direction: column;
  row-gap: 8px;

  &__title {
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    color: #9393a3;
  }

  &__description {
    font-weight: 400;
    margin: 0;
    color: #191919;
    text-decoration: none;

    @include hover(text-decoration, underline);
  }
}
